import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, FormControl } from '@angular/forms';
import { Router } from '@angular/router';

import { PostService } from '../services/post.service';
import { Post } from '../models/Post.model';

export interface Status {
  id: number;
  status: string;
}

@Component({
  selector: 'app-new-post',
  templateUrl: './new-post.component.html',
  styleUrls: ['./new-post.component.scss']
})
export class NewPostComponent implements OnInit {

  postForm: FormGroup;
  postsArrayLength: number = 0;
  statusChoices: Status[] = [
    { id: 0, status: 'Découverte'},
    { id: 1, status: 'Postulée'},
    { id: 2, status: 'Réponse'}
  ]

  constructor(private formBuilder: FormBuilder,
              private postService: PostService,
              private router: Router) { }

  ngOnInit() {
    console.log('new-post ngOnInit');

    this.initForm();

  }

  initForm() {
    this.postsArrayLength = this.postService.postsLength;
    console.log(this.postsArrayLength);


    this.postForm = this.formBuilder.group({
      title: '',
      content: '',
      status: this.formBuilder.array([]),
      index: this.postsArrayLength,

    });

  }

  onChange(status: string, isChecked: boolean) {
    const statusChoice = (this.postForm.controls.status as FormArray);

    if (isChecked) {
      statusChoice.push(new FormControl(status));
    } else {
      const index = statusChoice.controls.findIndex(x => x.value === status);
      statusChoice.removeAt(index);
    }
  }

  onSubmitForm() {
    console.log('onSubmit');

    const formValue = this.postForm.value;
    console.log(formValue);

    const newPost = new Post(
      formValue['title'],
      formValue['content'],
      formValue['status'],
      formValue['index']
    );

    this.postService.addPost(newPost);
    this.postService.synchronisePosts();

    this.router.navigate(['/post-list'])

  }

}
